import {
    ORDER_URL_ERROR,
    ORDER_URL_SUCCESS,
    ORDER_URL_REQUEST,
} from '../../actions/componentAction/components';
import { TComponentsActions } from '../../../utils/types/actionComponentsTypes';

export type TOrderState = {
    isOrderSend: boolean;
    isOrderSuccess: boolean;
    isOrderError: boolean;
};
const initialState: TOrderState = {
    // 
    isOrderSend: false,

    // 
    isOrderSuccess: false,

    //  
    isOrderError: false,
};

export const orderReducer = (
    state: TOrderState = initialState,
    action: TComponentsActions
): TOrderState => {
    switch (action.type) {
        case ORDER_URL_REQUEST: {
            return {
                ...state,
                isOrderError: false,
                isOrderSuccess: false,
                isOrderSend: true,
            };
        }

        case ORDER_URL_SUCCESS: {
            return {
                ...state,
                isOrderSend: false,
                isOrderError: false,
                isOrderSuccess: true,
            };
        }

        case ORDER_URL_ERROR: {
            return {
                ...state,
                isOrderSend: false,
                isOrderError: true,
                isOrderSuccess: false,
            };
        }

        //
        default: {
            return state;
        }
    }
};
